import React, { Component } from 'react'
import './index.css';

export default class GalleryPage extends Component {
    state={
        url:"data:image/jpeg;base64,"
    }
    render() {
        return (
            <div className="g_p_container">
                <div className="g_p_wrapper">
                    <h1 className="g_p_heading">Have a look at beatiful hotels</h1>
                   
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                     
                 <img src={this.state.url} className="g_p_image"/>
                       
                </div>
            </div>
        )
    }
}
